/* roboto-slab-regular - latin */
.logo{
    margin-left: 2rem;
    display: inline-flex;
    overflow: visible;
    align-items: center;
}

.logo p{
    display: flex;
    align-items: center;
    font-size: 200%;
    font-weight: 900;
}

.logo .color{
    color: #C09AFC;
    margin-left: 1rem;
}

footer .background{
    background: linear-gradient(#65004c30, #000030);
    height: fit-content;
}

.footer{
    justify-content: center;
    display: flex;
    height: fit-content;
}

.thispage, .navfooter, .general{
    padding: 7rem;
    margin-top: 7vh;
    padding-top: 3rem;
    padding-bottom: 0.5rem;
    border-left-style: solid;
    border-left-width: 2px;
    border-left-color: black;
    justify-content: center;
}

.thispage{
    border-left: none;
}

.thispage li a, .navfooter li a, .general li a{
    text-decoration: none;
    color: white;
}

.thispage li, .navfooter li, .general li{
    list-style: none;
    margin-bottom: 0.5rem;
    list-style-type: none;
}

.thispage li:nth-child(1), .navfooter li:nth-child(1), .general li:nth-child(1){
    margin-bottom: 0.7rem;
    font-size: 175%;
}

.thispage ul, .navfooter ul, .general ul{
    display: inline-block;
    align-items: start;
    padding: 0;
}

.c{
    background-color: #000030;
    box-shadow: 0 -5px 20px #00000061;
    height: 5vh;
    display: flex;
    place-items: center;
}

.c p{
    margin-left: 2rem;
}

footer{
    transform: translateY(-75%);
    z-index: 10000;
    height: fit-content;
}

.start{
    height: 1vh;
}