.container {
    display: flex;
}

.tab {
    display: flex;
    justify-content: center;
    text-align: center;

    margin: 0;
    flex-grow: 1;
    
}



.selected {
    display: flex;
    flex-grow: 1;
    text-align: center;
    margin: 0;
    padding: 30px 100px;
    background-color: lightgrey;
    color: black;
    border-radius: 20px 20px 0 0;
    flex-grow: 1;
}

.unselected {
    display: flex;
    flex-grow: 1;
    margin: 0;
    padding: 30px 100px;
    background-color: darkgrey;
    color: black;
    border-radius: 20px 20px 0 0;
}