body{
    font-family: Roboto Slab;
    background-color: #00061C;
}

/* roboto-slab-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/roboto-slab-v34-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
  }  

.above{
    background: conic-gradient(from 0.2525turn at 0% 85%, #423769, 100deg, #382D61);
    display: grid;
    place-items: center;
    text-align: center;
    padding: 1rem;
    padding-bottom: 2.5rem;
}

.above h1{
    margin: 0;
    padding: 0;
}

.above h2{
    padding: 0;
    margin: 0;
}

.above p{
    display: flex;
    flex-grow: 1;
    place-items: center;
    width: 25%;
    text-align: left;
    padding: 0.5rem;
    margin: 0;
}

.first{
    background-color: #261C49;
    padding: 4rem 1.5rem 2rem 6rem;
    display: flex;
    align-items: center;
    background: conic-gradient(from -0.258turn at 100% 85%, #261C49, 100deg, #28195E);
}

.first h1{
    margin: 0;
}

.first .text{
    display: block;
    flex-grow: 0.25;

}

.first .imgcontainer{
    
    flex-grow: 1;
    display: flexbox;
    align-items: center;

}

.first .imgcontainer img{
    position: relative;
    float: left;
    border-radius: 20px;
    margin: 0.25rem;
}


.second{
    padding: 3rem;
    background-color: #140F28;
    background: linear-gradient(#140F28 80%, #140f284f);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
}

.second .text{
    display: block;
}

.second h1{
    margin: 0;
    padding: 0;
}

.background{
    background: url('TechStackGraphics/Cog_Design.png');
    background-size: 30%;
    background-position: 80vw 50vh;
    background-repeat: no-repeat;
    background-color: #00023b;
    margin: 0;
}

.TechStack{
    display: flex;
    justify-content: center;
    align-items: baseline;
    padding: 4rem;
    padding-right: 0;
    padding-left: 0;
    height: fit-content;
    margin: 0;
}

.TechStack .list{
    width: fit-content;
    height: fit-content;
    padding: 0;
}

.TechStack .list .inlinelist button{
    border-radius: 10px;
    font-size: 125%;
    padding: 0 1rem;
    width: 15vw;
    height: 80px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    flex-grow: 1;
    margin: 0.15rem;
    border-style: none;
}

.TechStack .list .inlinelist button img{
    display: inline-block;
    position: relative;
}

.TechStack .list .inlinelist button p{
    display: inline-block;
    font-size: 120%;
    font-weight: 600;
    margin-left: 0.5rem;
}

.TechStack .text{
    flex-grow: 2;
    display: flex;
    align-items: start;
    margin-left: 2.5%;
}

.TechStack .list .inlinelist .left1{
    width: 40px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left3{
    width: 10px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left4{
    width: 120px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left5{
    width: 55px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left6{
    width: 70px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .unused{
    background-color: rgb(131, 131, 131);
    filter: blur(2px);
    width: 15vw;
}

.TechStack .text .bigger{
    font-size: 8.5vw;
    padding-top: 0;
    display: flex;
    margin: 0;
    line-height: 0.85;
    width: max-content;
}

.TechStack .text p{
    display: flex;
    color: #FFCCA3;
    margin: 0;
    font-size: 200%;
}

.inlinelist:nth-child(1){
    margin-right: 1.5rem;
}

.inlinelist:nth-child(2){
    margin-right: 4rem;
}

.inlinelist:nth-child(3){
    margin-right: 3rem;
}

.inlinelist:nth-child(4){
    margin-right: 5.5rem;
}

.inlinelist:nth-child(6){
    margin-right: 7rem;
}

.inlinelist{
    display: flex;
    width: max-content;
}

.unsereDesignApps{ 
    margin: 0;
    background: linear-gradient(#00023B, #00061C 10%);
    display: flex;
    
}

.unsereDesignApps .text{
    display: flex;
    align-items: start;
    margin-top: 5%;
    margin-bottom: 0;
    padding: 0;
}

.unsereDesignApps .text h1{
    margin: 0;
    font-size: 11vw;
    line-height: 0.95;
}

.unsereDesignApps .text p{
    color: #FFCCA3;
    font-size: 200%;
    margin: 0;
}

.unsereDesignApps img{
    position: relative;
    transform: translateY(-150px);
    filter: blur(5px);
}

.unsereDesignApps .designstack{
    display: inline-flex;
    flex-grow: 1;
    height: max-content;
    justify-content: center;
}