.menu {
    display: flex;
    justify-content: end;
    margin: 0;
    width: 100%;
    padding: 0;
  }
  
  .menu ul li a{
    text-decoration: none;
    color: white;
  }

  .menu ul li{
    list-style: none;
    height: fit-content;
  }

  .menu ul{
    display: flex;
    justify-content: end;
    gap: 100px;
    width: fit-content;
    height: fit-content;
    padding-right: 2rem;
    margin: 1.5rem;
  }

.logo{
    position: absolute;
    transform: translateY(-7.5%);
  }