.flexContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: 0;
    justify-content: space-around;
    height: 80vh;
}

.box {
    position: relative;
    align-content: center;
}

.text{
    display: flex;
    width: 30%;
    align-items: center;
    
    margin: 4rem;
    flex-direction: column;
}

.pic {
    
    width: 40%;
        display: grid;
        place-items: center;
    
}
.text .button{
    background-color: rgb(110, 110, 110);
    padding: 0.5rem;
    padding-left: 3.5rem;
    padding-right: 3.5rem;
    border-radius: 10px;
    font-size: 150%;
    font-weight: bold;
    cursor: pointer;
    border: none;
}

.text h1{
    margin: 1rem;
}

.text .p{
    margin: 1.5rem;
    margin-top: 0;
}

