.container {
    display: flex;
    margin-top: 1rem;
    margin-bottom: 0;
    height: 50px;
    background-color: gray;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    align-items: center;
}

.container p{
    display: flex;
    flex-grow: 1;
}

.before{
    display: flex;
    justify-content: center;
    content: '?';
    font-weight: bold;
    font-size: 150%;
    position: relative;
    align-items: center;
    background-color: white;
    color: black;
    padding: 0.5rem;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    margin: 1rem;
}

.back{
    display: flex;
    content: '^';
    justify-content: end;
    align-items: center;
    margin: 1rem;
    font-size: 200%;
    transition: all 1s;
    animation-name: flipback;
    animation-duration: 0.5s;
    animation-direction:reverse;
    cursor:default;
    transition: all 1s;
    animation-fill-mode: both;
}

.back2{
    display: flex;
    content: '^';
    justify-content: end;
    align-items: center;
    margin: 1rem;
    font-size: 200%;
    animation-name: flip;
    animation-duration: 0.5s;
    animation-direction:normal;
    cursor:default;
    transition: all 1s;
    animation-fill-mode: both;
}

