@keyframes flip{
    0%{
        display: flex;
        content: '^';
        justify-content: end;
        align-items: center;
        margin: 1rem;
        font-size: 200%;
        cursor:default;
        transition: all 1s;
    }
    100%{
        display: flex;
        justify-content: left;
        transform: rotateZ(180deg);
    }
}

@keyframes flipback{
    to{
        display: flex;
        justify-content: right;
        transform: rotateZ(180deg);
    }
}

.textbox {
    color: black;
    background-color: lightgray;
    margin: 0;
}

.textbox p{
    padding: 1rem;
    margin: 0;
}
